/* eslint-disable @typescript-eslint/no-explicit-any */
import { Component, OnInit } from '@angular/core';
import {
  AddAddressDto,
  AddressService,
  DetailLocation, getDetailLocationByVillageIdDto, getDetailLocationDto, LocationApiService,
  RequestDetailAddressDto, ValidationService
} from '@youtap-web-lib';
import { ModalService } from '../../../shared/service/modal/modal.service';
import { ModalSuccessLocationComponent } from '../../location/modal-success-location/modal-success-location.component';
import { Location } from '@angular/common';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthData } from '../../../shared/data/auth.data';
import { ActivatedRoute } from '@angular/router';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'add-address',
  templateUrl: './add-address.component.html'
})
export class AddAddressComponent implements OnInit {
  addressForm!: FormGroup;
  id = '';
  pickLocationModal = false;
  pickLocationTemp!: DetailLocation;
  addressMap = '';
  inputTextLabelAddress: string = '';
  inputLengthLabelAddress: number = 0;
  loading = false;

  constructor(
    private location: Location,
    private addressService: AddressService,
    private modalService: ModalService,
    private formBuilder: FormBuilder,
    private authData: AuthData,
    private route: ActivatedRoute,
    private locationApiService: LocationApiService,
    private gaService: GaService
  ) {
    this.addressForm = this.formBuilder.group({
      addressLabel: ['', Validators.compose([Validators.required, Validators.maxLength(30)])],
      addressFull: ['', Validators.compose([Validators.required])],
      postalCode: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(10),
          ValidationService.numValidator
        ])
      ],
      recipientName: ['', Validators.compose([Validators.required])],
      recipientPhone: [
        '',
        Validators.compose([
          Validators.required,
          Validators.maxLength(20),
          ValidationService.numValidator
        ])
      ],
      provinceId: ['', Validators.compose([Validators.required])],
      regencyId: ['', Validators.compose([Validators.required])],
      districtId: ['', Validators.compose([Validators.required])],
      villageId: ['', Validators.compose([Validators.required])],
      latitude: [null, Validators.compose([Validators.required])],
      longitude: [null, Validators.compose([Validators.required])]
    });
  }

  ngOnInit() {
    this.id = this.route.snapshot?.queryParamMap.get('id') ?? '';
    if (this.id) {
      this.getDetailAddress();
      this.addressForm.get('addressLabel')?.valueChanges.subscribe(value => {
        this.inputLengthLabelAddress = value.length;
      });
    }
  }

  getDetailLocation(lat: number, long: number) {
    const data: getDetailLocationDto = {
      detailLocationInput: {
        latitude: lat,
        longitude: long,
      },
    };

    this.locationApiService.detailLocation(data).subscribe({
      next: ({ data }) => {
        this.addressMap = data.detailLocation.addressDetail;
      }
    })
  }

  onInputChangeAddress(event: any) {
    this.inputTextLabelAddress = event.target.value;
    if (this.inputTextLabelAddress.length > 30) {
      this.inputTextLabelAddress = this.inputTextLabelAddress.slice(0, 30);
    }
  }

  getDetailAddress() {
    this.loading = true;
    const data: RequestDetailAddressDto = {
      id: this.id
    };

    this.addressService.detailAddress(data).subscribe({
      next: ({ data }) => {
        this.addressForm.controls['addressLabel'].setValue(
          data.detailAddress.name
        );
        this.addressForm.controls['addressFull'].setValue(
          data.detailAddress.addressDetail
        );
        this.addressForm.controls['provinceId'].setValue(
          data.detailAddress.province.id
        );
        this.addressForm.controls['regencyId'].setValue(
          data.detailAddress.regency.id
        );
        this.addressForm.controls['districtId'].setValue(
          data.detailAddress.district.id
        );
        this.addressForm.controls['villageId'].setValue(
          data.detailAddress.village.id
        );
        this.addressForm.controls['postalCode'].setValue(
          data.detailAddress.postalCode
        );
        this.addressForm.controls['recipientName'].setValue(
          data.detailAddress.recipientName
        );
        this.addressForm.controls['recipientPhone'].setValue(
          data.detailAddress.recipientPhone
        );
        this.addressForm.controls['latitude'].setValue(
          data.detailAddress.latitude
        );
        this.addressForm.controls['longitude'].setValue(
          data.detailAddress.longitude
        );
        this.getDetailLocation(data.detailAddress.latitude, data.detailAddress.longitude);
        this.loading = false;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  selectLocation($event: any) {
    this.pickLocationTemp = $event;
  }

  save() {
    if (this.id) {
      this.gaService.addEvent('Edit_Address', 'address', '');
      this.upsertAddress();
    } else {
      this.addAddress();
    }
  }

  addAddress() {
    this.loading = true;
    const data: AddAddressDto = {
      addAddressInput: {
        name: this.addressForm.controls['addressLabel'].value,
        addressDetail: this.addressForm.controls['addressFull'].value,
        provinceId: this.addressForm.controls['provinceId'].value,
        regencyId: this.addressForm.controls['regencyId'].value,
        districtId: this.addressForm.controls['districtId'].value,
        villageId: this.addressForm.controls['villageId'].value,
        postalCode: this.addressForm.controls['postalCode'].value,
        recipientName: this.addressForm.controls['recipientName'].value,
        recipientPhone: this.addressForm.controls['recipientPhone'].value,
        recipientEmail: '',
        latitude: this.addressForm.controls['latitude'].value,
        longitude: this.addressForm.controls['longitude'].value
      }
    };
    this.addressService.upsertAddress(data).subscribe({
      next: () => {
        this.loading = false;
        const modalRef = this.modalService.open(ModalSuccessLocationComponent, {
          title: '',
          closeButton: false,
          data: {
            content: 'Kamu Berhasil Menambah Alamat!'
          }
        });
        setTimeout(() => {
          modalRef.close('');
          this.location.back();
          setTimeout(() => {
            window.location.reload();
          }, 500);
        }, 2000);
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  upsertAddress() {
    this.loading = true;
    const data: AddAddressDto = {
      addAddressInput: {
        id: this.id,
        name: this.addressForm.controls['addressLabel'].value,
        addressDetail: this.addressForm.controls['addressFull'].value,
        provinceId: this.addressForm.controls['provinceId'].value,
        regencyId: this.addressForm.controls['regencyId'].value,
        districtId: this.addressForm.controls['districtId'].value,
        villageId: this.addressForm.controls['villageId'].value,
        postalCode: this.addressForm.controls['postalCode'].value,
        recipientName: this.addressForm.controls['recipientName'].value,
        recipientPhone: this.addressForm.controls['recipientPhone'].value,
        recipientEmail: '',
        latitude: this.addressForm.controls['latitude'].value,
        longitude: this.addressForm.controls['longitude'].value
      }
    };
    this.addressService.upsertAddress(data).subscribe({
      next: () => {
        this.loading = false;
        const modalRef = this.modalService.open(ModalSuccessLocationComponent, {
          title: '',
          closeButton: false,
          data: {
            content: 'Kamu Berhasil Merubah Alamat!'
          }
        });
        setTimeout(() => {
          modalRef.close('');
          this.location.back();
          setTimeout(() => {
            window.location.reload();
          }, 500);
        }, 2000);
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  setPickLocation() {
    this.addressForm.controls["provinceId"].reset()
    this.addressForm.controls["provinceId"].setValue(this.pickLocationTemp?.village?.district?.regency?.province ? this.pickLocationTemp.village.district?.regency?.province?.id : "");
    this.addressForm.controls["regencyId"].setValue(this.pickLocationTemp?.village?.district?.regency ? this.pickLocationTemp.village.district?.regency?.id : "");
    this.addressForm.controls["districtId"].setValue(this.pickLocationTemp?.village?.district ? this.pickLocationTemp.village.district?.id : "");
    this.addressForm.controls["villageId"].setValue(this.pickLocationTemp?.village ? this.pickLocationTemp.village?.id : "");
    this.addressForm.controls["latitude"].setValue(this.pickLocationTemp.latitude);
    this.addressForm.controls["longitude"].setValue(this.pickLocationTemp.longitude);
    this.addressMap = `${this.pickLocationTemp.addressDetail.substring(0, 50)}...`;
    this.pickLocationModal = !this.pickLocationModal;
  }

  changeProvince() {
    this.addressForm.controls["regencyId"].reset();
    this.addressForm.controls["districtId"].reset();
    this.addressForm.controls["villageId"].reset();
    this.addressForm.controls["postalCode"].reset();
  }

  changeRegency() {
    this.addressForm.controls["districtId"].reset();
    this.addressForm.controls["villageId"].reset();
    this.addressForm.controls["postalCode"].reset();
  }

  changeDistrict() {
    this.addressForm.controls["villageId"].reset();
    this.addressForm.controls["postalCode"].reset();
  }

  setDetailLocationByVillageId() {
    const data: getDetailLocationByVillageIdDto = {
      detailLocationByVillageIdInput: {
        villageId: this.addressForm.controls["villageId"].value,
      }
    }
    this.locationApiService.detailLocationByVillageId(data).subscribe({
      next: (data) => {
        this.addressForm.controls['latitude'].setValue(data.data.detailLocationByVillageId.latitude);
        this.addressForm.controls['longitude'].setValue(data.data.detailLocationByVillageId.longitude);
      }
    })
  }
}
