import {Component, OnInit} from '@angular/core';
import {
  Address,
  AddressService, DeleteAddressInput,
  ListAddressDto,
  RequestDetailAddressDto,
  ResponseListAddressDto,
  SetDefaultAddressInput
} from "@youtap-web-lib";
import {Router} from "@angular/router";
import {AuthData} from "../../../shared/data/auth.data";
import {WindowActionService} from "../../../shared/service/window-action.service";
import { Location } from '@angular/common';
import { GaService } from '../../../shared/service/ga.service';

@Component({
  selector: 'list-address',
  templateUrl: './list-address.component.html',
  styleUrls: ['./list-address.component.scss']
})
export class ListAddressComponent implements OnInit{
  searchValue = "";
  selectedId = "";
  deletedId = "";
  loading = true;
  loadingDefault = false;
  listAddress!: ResponseListAddressDto;
  confirmChange = false;
  confirmDelete = false;
  successDelete = false;

  constructor(
    private addressService: AddressService,
    private router: Router,
    public authData: AuthData,
    private windowActionService: WindowActionService,
    private location: Location,
    private gaService: GaService
    ) {
  }

  ngOnInit(): void {
    this.getListAddress();
  }

  selectAddress(item: Address) {
    this.selectedId = item.id;
  }

  editAddress(id: string) {
    this.gaService.addEvent('Chart_click_button_ubah_address', 'address', '');
    this.router.navigate(["/address/edit"], {
      queryParams: { id }
    });
  }

  getListAddress(search = "", fromEmpty = false) {
    this.loading = true;
    const data: ListAddressDto = {
      listAddressInput: {
        page: 0,
        limit: 25,
        search: {
          keyword: search
        }
      }
    };
    this.addressService.listAddress(data).subscribe({
      next: ({ data }) => {
        if (fromEmpty) {
          this.searchValue = '';
        }
        this.loading = false;
        this.listAddress = data;
      },
      error: () => {
        this.loading = false;
      }
    });
  }

  setDefaultAddress() {
    this.gaService.addEvent('Change_Main_Address', 'address', '');
    this.loadingDefault = true;
    this.confirmChange = false;
    const data: SetDefaultAddressInput = {
      id: this.selectedId
    };
    this.addressService.setDefaultAddress(data).subscribe({
      next: () => {
        this.setCoverage();
        this.confirmChange = true;
      },
      error: () => {
        this.loadingDefault = false;
      }
    });
  }

  handleCloseModal() {
    this.windowActionService.reload();
  }

  setCoverage() {
    const data: RequestDetailAddressDto = {
      id: this.selectedId
    };
    this.addressService.detailAddress(data).subscribe({
      next: () => {
        this.loadingDefault = false;
        this.location.back();
        setTimeout(() => {
          this.windowActionService.reload();
        }, 500)
      },
      error: () => {
        this.loadingDefault = false;
      }
    });
  }

  selectDeletedId(id: string) {
    this.deletedId = id;
    this.confirmDelete = !this.confirmDelete;
  }

  deleteAddress() {
    this.gaService.addEvent('Delete_Address', 'address', '');
    const data: DeleteAddressInput = {
      id: this.deletedId
    };
    this.addressService.deleteAddress(data).subscribe({
      next: () => {
        this.confirmDelete = !this.confirmDelete;
        this.deletedId = "";
        this.successDelete = true;
        setTimeout(() => {
          this.successDelete = false;
          this.windowActionService.reload();
        }, 1500);
      }
    });
  }

  addAddressUrl() {
    this.gaService.addEvent('Add_Address', 'address', '');
    this.router.navigate(["/address/add"], {});
  }
}
